/* ==========================================================================
   WooCommerce Variation styles - begin
   ========================================================================== */

.mkdf-woocommerce-page .mkdf-content {
    .variations {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        border: none;
        padding: 0;

        tr {
            display: block;
            padding: 0 0 20px;
            border: 0;

            &:last-child {
                margin: 0;
            }
        }

        td {
            padding: 0;
            text-align: initial;

            &.label {
                padding-right: 10px;
            }

            &.value {
                white-space: nowrap;

                select {
                    width: auto;
                    min-width: 200px;
                }

                .select2 {
                    margin: 0;
                }
            }
        }

        .reset_variations {
            margin: 0 0 0 15px;
            color: $default-text-color;
        }
    }

    .single_variation_wrap {
        margin: 35px 0 0;

        .single_variation {
            margin: 0 0 35px;
        }
    }

    table.group_table {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        border: none;
        padding: 0;

        tbody {
            display: block;
        }

        tr {
            display: block;
            padding: 0 0 10px;
            border: 0;

            &:last-child {
                margin: 0;
            }
        }

        td {
            padding: 0 17px 10px 0;
            text-align: left;

            &.price {
                display: table-cell;
                vertical-align: middle;
            }

            &:last-child {
                padding: 0 0 10px;
            }
        }

        .mkdf-quantity-buttons {
            margin: 0;
        }

        a {
            color: $default-text-color;

            &:hover {
                color: $default-text-color;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Variation styles - end
   ========================================================================== */