/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
    @include mkdfRelativeHolderLayout();

    .mkdf-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;

        &:hover {
            .price {
                opacity: 0;
                @include mkdfTransition(opacity .1s ease-out 0s);
            }

            .button,
            .added_to_cart.wc-forward {
                opacity: 1;
                @include mkdfTransition(opacity .2s ease-out .15s);
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // image and link

    .mkdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        .touchevents & {
            cursor: pointer;
        }

        .mkdf-pli-image {
            @include mkdfRelativeHolderLayout();

            &:before {
                content:"";
                background-color:rgba(0,0,0,.4);
                opacity:0;
                @include mkdfTransition(all .3s ease-out);
                @include mkdfAbsoluteHolderLayout();
            }

            img {
                display: block;
                width: 100%;
            }

            .mkdf-pli-onsale,
            .mkdf-pli-out-of-stock,
            .mkdf-pli-new-product {
                @include mkdfWooCommerceMarksGeneralStyle();
            }

            .mkdf-pli-onsale {
                @include mkdfWooCommerceSaleStyle();
            }

            .mkdf-pli-out-of-stock {
                @include mkdfWooCommerceOutOfStockStyle();
            }

            .mkdf-pli-new-product {
                @include mkdfWooCommerceNewProductStyle();
            }
        }

        .mkdf-pli-link {
            @include mkdfAbsoluteHolderLayout();
        }

            &:hover {
                .mkdf-pli-image:before { 
                    opacity:1;
                }
            }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // product info

    .mkdf-pli-text-wrapper {
        @include mkdfRelativeHolderLayout();

        margin: 25px 0 24px;
        text-align: center;
    }

    .mkdf-pli-category {
        @include mkdfH5();

        margin-bottom: 9px;
    }

    .mkdf-pli-title {
        margin: 0 0 5px;
    }

    .mkdf-pli-excerpt {
        color: $default-text-color;
        line-height: 1.3em;
        margin: 0 0 3px;
    }

    .mkdf-pli-rating-holder {
        @include mkdfRelativeHolderLayout();

        line-height: 1;
        margin: 10px 0 3px;
    }

    .mkdf-pli-rating {
        @include mkdfWooCommerceRatingsStyle();
    }

    .mkdf-pli-cart-price-holder {
        @include mkdfProductListCartPrice();

        .button {
            //font-size: 12px;
            //font-weight: 700;
            //letter-spacing: 0.25em;
            //text-transform: uppercase;
            position:relative;
            text-decoration:none;
            width:auto;
            left:auto;

            &:before,
            &:after {
                content:"";
                position:absolute;
                bottom:12px;
                left:0;
                width:100%;
                height:1px;
                background-color:currentColor;
            }

            &:before {
                transform-origin: right;

                @include mkdfTransition(all .2s ease-in 0s);
            }

            &:after {
                transform-origin: left;

                @include mkdfTransform(scale(0, 1));
                @include mkdfTransition(all .25s ease-out .25s);
            }

            &:hover {
                &:before {
                    @include mkdfTransform(scale(0, 1));
                }

                &:after {
                    @include mkdfTransform(scale(1, 1));
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // product info skins

    &.mkdf-product-info-light {
        .mkdf-pli-category,
        .mkdf-pli-title,
        .mkdf-pli-excerpt,
        .mkdf-pli-rating,
        .price {
            color: #ffffff;
        }

        .mkdf-pli-category,
        .mkdf-pli-title {
            a:hover {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layout

    .mkdf-pli {
        float: left;
    }

    &.mkdf-large-space {
        margin: 0 0 -50px;
    }

    &.mkdf-normal-space {
        margin: 0 0 -30px;
    }

    &.mkdf-small-space {
        margin: 0 0 -20px;
    }

    &.mkdf-tiny-space {
        margin: 0 0 -10px;
    }

    $standard_list_columns: ('two', 'three', 'four', 'five', 'six');

    @for $i from 0 to length($standard_list_columns) {
        &.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
            $column_width: 100%/($i+2);

            .mkdf-pli {
                width: $column_width;

                @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                    &:nth-child(#{$i+2}n+1) {
                        clear: both;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */