/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

//@media only screen and (min-width: 769px) {
//    .mkdf-woo-single-page
//}

.mkdf-woo-single-page {
    .product {
        @include mkdfRelativeHolderLayout();

        float: none;
        margin: 0 0 38px;
    }

    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();

        clear: both;
        margin: 0 0 70px;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // variations images

    .product-type-variable {
        .mkdf-single-product-content {
            .images {
                @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                    &.woocommerce-product-gallery--with-images {
                        .woocommerce-product-gallery__image {
                            &:first-child {
                                img {
                                    opacity: 1;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // images

    .mkdf-single-product-content {
        .images {
            box-sizing: border-box;
            display: inline-block;
            float: left;
            margin: 0;
            padding: 0 15px 0 0;
            position: relative;
            vertical-align: middle;
            width: 50%;

            @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                .woocommerce-product-gallery__wrapper {
                    width: 100%;
                    margin: 0;
                }

                &.woocommerce-product-gallery--without-images {
                    .woocommerce-product-gallery__wrapper {
                        img {
                            width: 100%;
                        }
                    }
                }

                &.woocommerce-product-gallery--with-images {
                    $thumb-width: 190px;
                    $thumb-padding: 30px;

                    .woocommerce-product-gallery__image {
                        &:first-child {
                            float: right;
                            height: $thumb-width * 3 + $thumb-padding * 2;
                            width: calc(100% - #{$thumb-width + $thumb-padding});

                            a {
                                background-position: center;
                                background-size: cover;
                            }

                            img {
                                height: $thumb-width * 3 + $thumb-padding * 2;
                                opacity: 0;
                            }
                        }

                        &:not(:first-child) {
                            box-sizing: border-box;
                            clear: left;
                            float: left;
                            height: auto;
                            padding: 0 0 $thumb-padding;
                            width: $thumb-width;
                        }
                    }
                }
            }

            .woocommerce-product-gallery__wrapper {
                display: inline-block;
                overflow: hidden;
                position: relative;
                vertical-align: top;

                a {
                    @include mkdfRelativeHolderLayout();

                    img {
                        display: block;
                        position: relative;
                        width: 100%;
                    }
                }
            }

            .woocommerce-product-gallery__trigger {
                height: 30px;
                line-height: 30px;
                position: absolute;
                right: 39px;
                text-align: center;
                top: 10px;
                width: 30px;
                z-index: 10;
            }

            .woocommerce-product-gallery__image {
                .zoomImg {
                    cursor: crosshair;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // summary

    .mkdf-single-product-summary {
        box-sizing: border-box;
        display: inline-block;
        float: left;
        padding: 0 0 0 15px;
        position: relative;
        vertical-align: middle;
        width: 50%;

        .mkdf-single-product-title {
            display: block;
            margin: 0 0 13px;
        }

        .price {
            @include mkdfWooCommercePriceStyle(true);
        }

        .woocommerce-product-rating {
            @include mkdfRelativeHolderLayout();

            line-height: 1;
            margin-bottom: 20px;
        }

        .woocommerce-review-link {
            @include mkdfH5();
        }

        .woocommerce-product-details__short-description {
            @include mkdfRelativeHolderLayout();

            margin: 0 0 41px;

            p {
                margin: 0;
            }
        }

        p.stock.out-of-stock,
        p.stock.in-stock {
            font-weight: 700;
            margin: 0 0 39px;
        }

        form.cart {
            @include mkdfRelativeHolderLayout();

            margin: 10px 0 48px;
        }

        .mkdf-quantity-buttons {
            margin: 0 20px 0 0;
        }

        .product_meta {
            > span {
                display: block;

                @include mkdfH5();

                a,
                .sku {
                    @include mkdfParagraph();

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }

        .mkdf-woo-social-share-holder {
            @include mkdfRelativeHolderLayout();

            margin: 0;

            > * {
                display: table-cell;
                vertical-align: middle;
            }

            > span {
                padding: 0 9px 0 0;

                @include mkdfH5();
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tabs

    .woocommerce-tabs {
        @include mkdfRelativeHolderLayout();

        margin: 0;

        ul.tabs {
            @include mkdfRelativeHolderLayout();

            clear: both;
            list-style: none;
            margin: 0 0 20px 0;
            padding: 0;

            > li {
                background: transparent;
                display: inline-block;
                position: relative;
                vertical-align: middle;
                margin-right: 26px;

                &:last-child {
                    margin: 0;
                }

                a {
                    @include mkdfH5();

                    color: $default-text-color;
                    display: inline-block;
                    padding: 15px 31px;
                    position: relative;
                    vertical-align: top;
                }

                &.active,
                &:hover {
                    background: $default-text-color;

                    a {
                        color: #ffffff;
                    }
                }
            }
        }

        .entry-content {
            @include mkdfRelativeHolderLayout();

            > h2 {
                display: none;
            }

            > p {
                margin: 28px 0;
            }
        }

        table {
            margin: 28px 0;

            tr, th {
                border: 0;
            }

            td, th {
                padding: 0;
                text-align: left;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }

            th {
                @include mkdfH5();

                padding: 0 10px 0 0;
                width: 1%;
            }

            td {
                width: 99%;
            }
        }

        #reviews {
            @include mkdfRelativeHolderLayout();

            margin: 28px 0;

            h2 {
                padding-top: 7px;

                @include mkdfH4();
            }

            ol.commentlist {
                list-style: none;

                li {
                    margin: 0 0 15px;

                    &:last-child {
                        margin: 0;
                    }
                }

                .comment_container {
                    @include mkdfRelativeHolderLayout();

                    img {
                        float: left;
                        width: 70px;
                    }
                }

                .comment-text {
                    padding: 0 0 0 95px;

                    p {
                        margin: 0;
                    }

                    .star-rating {
                        top: -8px;
                    }

                    .meta {
                        margin-top: -8px;

                        strong {
                            @include mkdfH5();
                        }
                    }
                }
            }

            .comment-respond {
                margin-top: 15px;

                .comment-reply-title {
                    @include mkdfH5();

                    margin: 0 0 10px;
                }

                p {
                    margin: 0;
                }

                .stars {
                    overflow: hidden;

                    a {
                        color: transparent;
                        display: inline-block;
                        font-size: 0;
                        margin: 0;
                        position: relative;

                        @include mkdfWooCommerceSingleProductRatingsStyle();
                    }
                }
            }
        }
    }

    .related.products,
    .upsells.products {
        > h2 {
            display: block;
            margin: 0;
            text-align: center;
            padding: 80px 0 50px 0;

            @include mkdfH3();
        }

        .product {
            .mkdf-pl-text-wrapper {
                margin-bottom: 0;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */