/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */

.mkdf-woocommerce-page {
    table.cart {
        margin: 37px 0 0;

        thead {
            tr {
                border: 0;
                border-bottom: 1px solid $default-border-color;

                th {
                    color: $default-text-color;
                    padding: 10px 0;
                    text-align: left;
                }
            }
        }

        tbody {
            tr {
                border: 0;
            }
        }

        tr.cart_item {
            border-bottom: 1px solid $default-border-color;

            td {
                padding: 25px 0;
                text-align: inherit;
                vertical-align: middle;

                &.product-remove {
                    padding-left: 10px;
                    padding-right: 10px;

                    a {
                        color: $elements-color-two;
                        font-size: 0;
                        line-height: 1;

                        &:after {
                            color: inherit;
                            content: "\f00d";
                            display: inline-block;
                            font-family: "FontAwesome";
                            font-size: 15px;
                            font-style: normal;
                            font-variant: normal;
                            font-weight: normal;
                            line-height: inherit;
                            position: relative;
                            text-transform: none;
                            top: -6px;
                            vertical-align: middle;
                            speak: none;
                            text-rendering: auto;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }

                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }

                &.product-thumbnail {
                    padding-right: 10px;
                    width: 86px;

                    a, img {
                        display: block;
                    }
                }

                .variation {
                    > * {
                        display: inline-block;
                        margin: 0;
                        padding: 0;
                        vertical-align: middle;
                    }

                    p {
                        margin: 0;
                    }
                }
            }
        }

        td.actions {
            padding: 35px 0 36px;
            text-align: inherit;

            .coupon {
                display: inline-block;
                float: left;
                position: relative;
                vertical-align: middle;

                label {
                    display: none;
                }

                input {
                    margin: 0 22px 0 0;
                }
            }

            > input, button {
                float: right;
            }

            button {
                margin-top: 6px !important;
            }
        }
    }

    .cart-collaterals {
        @include mkdfRelativeHolderLayout();

        margin: 0 0 39px;

        h2 {
            display: block;
            margin: 0 0 26px;
            position: relative;
        }

        table {
            margin: 0 0 35px;

            tr {
                border: 0;
                border-bottom: 1px solid $default-border-color;
            }

            td, th {
                padding: 14px 0;
                text-align: left;
                vertical-align: middle;
            }

            th {
                border: 0;
                padding-right: 5px;
                width: 20%;
            }
        }

        tr.shipping {
            #shipping_method {
                list-style: none;
                margin: 0 0 7px;

                li {
                    margin: 3px 0;
                    padding: 0;

                    > * {
                        display: inline-block;
                        margin: 0;
                        vertical-align: middle;
                    }
                }
            }
        }

        .woocommerce-shipping-calculator {
            .shipping-calculator-button {
                display: block;
                margin: 0 0 10px;
            }

            p {
                margin: 0;
            }
        }
    }

    .cross-sells {
        border-top: 1px solid $default-border-color;
        margin: 30px 0 0;

        > h2 {
            display: block;
            margin: 0;
            padding: 72px 0 50px 0;
            text-align: center;
        }
    }

    .cart_totals {
        margin: 1px 0 20px;
    }
}

.mkdf-woocommerce-page {
    .cart-empty {
        display: block;
        margin: 40px 0 33px;
        position: relative;
        text-align: center;

        @include mkdfH6();
    }

    .return-to-shop {
        display: block;
        margin: 0 0 60px;
        position: relative;
        text-align: center;
    }
}

/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */