/* ==========================================================================
   WooCommerce Message styles - begin
   ========================================================================== */
.mkdf-woocommerce-page {
    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        background-color: transparent;
        border: 1px solid $default-border-color;
        display: block;
        line-height: 44px;
        list-style: none;
        margin: 0 0 30px;
        padding: 20px 23px !important;
        position: relative;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-text-color;
            }
        }

        a {
            float: right;
        }
    }

    .woocommerce-info {
        .showcoupon {
            color: $default-text-color;

            &:hover {
                color: $default-text-color;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Message styles - end
   ========================================================================== */