/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    @include mkdfTransform(translateZ(0));
    @include mkdfH5();

    box-shadow: $default-box-shadow;
    box-sizing: border-box;
    color: #ffffff;
    display: block;
    height: 30px;
    line-height: 30px;
    margin: 0;
    padding: 0 11px;
    position: absolute;
    text-align: center;
    top: 40px;
    width: auto;
    z-index: 100;
}

@mixin mkdfWooCommerceSaleStyle() {
    background-color: $first-main-color;
    left: 0;
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    background-color: $first-main-color;
    left: 0;
    z-index: 101;
}

@mixin mkdfWooCommerceNewProductStyle() {
    background-color: $default-text-color;
    right: 0;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle($single:false) {
    color: $default-text-color;
    display: block;
    line-height: 1;
    position: relative;

    * {
        font-size: inherit;
        line-height: inherit;
    }

    del {
        color: rgba($default-text-color, .8);
        margin: 0 8px 0 0;
    }

    ins {
        text-decoration: none;
    }

    @if $single {
        font-size: 20px;
        margin: 0 0 49px;

        del {
            font-size: 16px;
        }
    }
}

/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    color: $default-text-color;
    font-family: "FontAwesome";
    font-size: 14px;
    letter-spacing: 2px;
    line-height: 1;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    display: inline-block;
    position: relative;
    vertical-align: middle;

    @extend %star-ratings;

    overflow: hidden;

    &:before {
        content: "\f006\f006\f006\f006\f006";
    }

    span {
        left: 0;
        overflow: hidden;
        position: absolute;
        top: 0;

        &:before {
            content: "\f005\f005\f005\f005\f005";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    &:before {
        content: "\f006";
        display: inline-block;
        position: relative;
        vertical-align: middle;

        @extend %star-ratings;

        overflow: hidden;
    }

    &.active:after {
        bottom: 7px;
        content: "\f005\f005\f005\f005\f005";
        display: block;
        position: absolute;
        right: -4px;
        letter-spacing: 6px;
        z-index: -1;

        @extend %star-ratings;
    }
}

/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// product list cart and price styles - begin

@mixin mkdfProductListCartPrice() {
    $height: 38px;

    @include mkdfRelativeHolderLayout();

    height: $height;

    .price {
        @include mkdfAbsoluteHolderLayout();

        display: inline-block;
        left: 0%;
        line-height: $height;
        opacity: 1;
        vertical-align: middle;
        width: 100%;
        white-space: nowrap;
        @include mkdfTransition(opacity .2s ease-out .15s);

        ins {
            text-decoration: none;
        }
    }

    .button,
    .added_to_cart.wc-forward {
        @include mkdfAbsoluteHolderLayout();
        @include mkdfButtonSimple(!important);

        display: inline-block;
        font-size: 12px;
        font-weight: 700;
        left: 0%;
        width:100%;
        letter-spacing: 0.25em;
        line-height: $height;
        opacity: 0;
        text-align: center;
        text-decoration: underline;
        text-transform: uppercase;
        vertical-align: middle;
        white-space: nowrap;

        @include mkdfTransition(opacity .1s ease-out 0s);
    }

    .button.added {
        display: none;
    }

    &:hover {
        .price {
            opacity: 0;
            @include mkdfTransition(opacity .1s ease-out 0s);
        }

        .button,
        .added_to_cart.wc-forward {
            opacity: 1;
            @include mkdfTransition(opacity .2s ease-out .15s);
        }
    }
}