/* ==========================================================================
   WooCommerce MyAccount page styles - begin
   ========================================================================== */

.mkdf-woocommerce-page.woocommerce-account {
    .woocommerce-MyAccount-navigation,
    .woocommerce-MyAccount-content {
        box-sizing: border-box;
        display: inline-block;
        float: left;
        margin: 27px 0;
        position: relative;
        vertical-align: middle;
    }

    .woocommerce-MyAccount-navigation {
        padding: 0 15px 0 0;
        width: 28%;

        ul {
            border-right: 1px solid $default-border-color;
            list-style: none;
            margin: 0;
            padding: 0 15px 0 0;

            li {
                margin: 0;
                padding: 0;

                &:not(:last-child) {
                    margin: 0 0 18px;
                }
            }
        }
    }

    .woocommerce-MyAccount-content {
        padding: 0 0 0 30px;
        width: 72%;
    }

    .woocommerce {
        margin: 0 0 45px;

        form.login {
            margin: 0 0 34px;

            p {
                margin: 0;

                &.mkdf-login-form-text {
                    margin: 0 0 7px;
                }
            }

            input[type='checkbox'] {
                display: inline-block;
                margin: 0 3px 0 5px;
                position: relative;
                top: -1px;
                vertical-align: middle;
            }

            .lost_password {
                display: block;
                margin: 15px 0 0;
            }
        }

        form.edit-account {
            margin: 0 0 15px;

            p {
                margin: 0;
            }

            label {
                display: block;
                margin: 0 0 5px;
            }

            fieldset {
                > legend {
                    color: $default-text-color;
                    display: block;
                    margin: 0 0 10px;
                }
            }
        }

        > p {
            display: block;
            margin: 0 0 30px;
        }

        > h2,
        > header h2 {
            display: block;
            margin: 16px 0 25px;
        }

        table.shop_table {
            margin: 0 0 35px;
            padding: 0;

            tr {
                border: 0;
                border-bottom: 1px solid $default-border-color;
            }

            td, th {
                padding: 14px 0;
                text-align: left;
                vertical-align: middle;
            }

            th {
                border: 0;
            }

            td {
                &.order-actions {
                    text-align: right;
                }
            }

            .variation {
                > * {
                    display: inline-block;
                    margin: 0;
                    padding: 0;
                    vertical-align: middle;
                }

                p {
                    margin: 0;
                }
            }
        }

        .col2-set.addresses {
            @include mkdfRelativeHolderLayout();

            clear: both;
            margin: 0 0 15px;

            .col-1,
            .col-2 {
                display: inline-block;
                float: left;
                margin-right: 1%;
                position: relative;
                vertical-align: middle;
                width: 49%;
            }

            .col-2 {
                margin-left: 1%;
                margin-right: 0;
            }

            .title {
                margin: 4px 0 25px;
            }

            h3 {
                margin: 0;
            }

            address {
                font-style: normal;
            }
        }
    }

    .vc_row {
        .woocommerce {
            > h2 {
                font-size: 30px;
                line-height: 40px;
                margin: 0 0 25px;
            }

            form.login {
                margin: 0;

                p {
                    @include mkdfRelativeHolderLayout();

                    label {
                        &:not(.inline) {
                            color: $default-text-color;
                            display: block;
                            font-size: 18px;
                            line-height: 38px;
                            margin: 14px 0;
                        }

                        &.inline {
                            display: inline-block;
                            margin: 23px 0 0;
                            position: relative;
                            vertical-align: middle;
                        }
                    }

                    input.button {
                        margin: 23px 0 0;
                    }
                }
            }
        }
    }
}

.mkdf-woocommerce-page.woocommerce-account.woocommerce-edit-address {
    .woocommerce {
        > form {
            margin: 0 0 15px;

            > *:first-child {
                margin: 0 0 25px;
            }

            p {
                margin: 0;
            }

            label {
                display: block;
                margin: 0 0 5px;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce MyAccount page styles - end
   ========================================================================== */