/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {
    .mkdf-quantity-buttons {
        display: inline-block;
        position: relative;
        vertical-align: middle;

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            background-color: transparent;
            border: 1px solid $default-border-color;
            box-sizing: border-box;
            color: $default-text-color;
            display: inline-block;
            text-align: center;
            vertical-align: middle;
        }

        .mkdf-quantity-input {
            font-family: inherit;
            height: 44px;
            line-height: 42px;
            margin: 0 22px 0 0;
            outline: 0;
            padding: 0;
            position: relative;
            width: 44px;
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            border-left: 0;
            cursor: pointer;
            height: 22px;
            line-height: 20px;
            position: absolute;
            right: 0;
            width: 22px;

            @include mkdfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-quantity-plus {
            border-bottom: 0;
            top: 0;
        }

        .mkdf-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {
    label {
        @include mkdfH5();
    }

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {
        input[type="text"] {
            width: 200px;
        }
    }
}

/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */