/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    display: inline-block;
    height: 100%;
    padding: 0 10px;
    position: relative;
    vertical-align: middle;

    .touchevents & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        display: table;
        height: 100%;
        position: relative;
    }

    .mkdf-header-cart {
        display: table-cell;
        position: relative;
        vertical-align: middle;

        .mkdf-cart-icon {
            display: inline-block;
            position: relative;
            vertical-align: top;
        }

        .mkdf-cart-number {
            font-size: 12px;
            font-weight: 500;
        }

        &.mkdf-header-cart-icon-pack {
            .mkdf-cart-icon {
                font-size: 20px;
                line-height: 1em;

                * {
                    display: block;
                    line-height: inherit;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            .mkdf-cart-icon {
                > * {
                    vertical-align: middle;
                }

                > *,
                path {
                    fill: currentColor;
                }
            }
        }
    }

    &:hover {
        .mkdf-shopping-cart-dropdown {
            max-height:1500px;
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkdf-shopping-cart-dropdown {
    background-color: #000000;
    box-sizing: border-box;
    left: auto;
    position: absolute;
    right: 0;
    text-align: left;
    top: 100%;
    width: 325px;
    max-height:0;
    overflow:hidden;
    z-index: 1000;

    @include mkdfTransition(all 0.7s cubic-bezier(.53, .01, .18, 1));

    ul {
        margin: 40px 40px 50px;
        padding: 0;

        li {
            @include mkdfTableLayout();

            box-sizing: border-box;
            padding-bottom: 20px;

            &.mkdf-empty-cart {
                padding-top: 10px;

                h5 {
                    color: #ffffff;
                }
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkdf-item-image-holder {
        display: table-cell;
        height: 100%;
        position: relative;
        vertical-align: top;
        width: 80px;

        img {
            display: block;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();

        padding-left: 20px;
        vertical-align: top;

        .mkdf-product-title {
            box-sizing: border-box;
            display: block;
            line-height: 1.3em;
            margin: 0;
            padding: 0 20px 3px 0;

            a {
                color: #ffffff;

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .remove {
            color: $elements-color-two;
            font-size: 14px;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 0;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-quantity,
        .mkdf-price {
            color: #ffffff;
            display: inline-block;
            padding-bottom: 10px;
        }
    }

    .mkdf-cart-bottom {
        padding: 0;

        .mkdf-subtotal-holder {
            display: table;
            padding: 14px 0 32px;
            position: relative;
            width: 100%;

            > * {
                color: #ffffff;
                display: table-cell;
                vertical-align: top;
            }
        }

        .mkdf-view-cart,
        .mkdf-checkout {
            @include mkdfButtonDefaultStyle();
            @include mkdfButtonSmallParams();

            text-align: center;
            width: 100%;

            &:hover {
                @include mkdfButtonOutlineHoverColor();
            }
        }

        .mkdf-view-cart {
            background-color: transparent;
            border: 2px solid #ffffff;
            color: #ffffff;
            margin-bottom: 10px;
        }

        .mkdf-checkout {
            background-color: #ffffff;
            border: 2px solid #ffffff;
            color: $default-text-color;
        }
    }
}

.mkdf-vertical-menu-area {
    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}

/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */