/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 80px;
    $inner-padding: 15px; // distance between image and content
    $list-item-padding: 9px; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }

    &.widget_layered_nav,
    &.widget_product_categories {
        @include mkdfH5();
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include mkdfH5();
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 7px;
            margin-top: 7px;

            span {
                max-width: 100px;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 20px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: $default-text-color;
                height: 1px;
                position: relative;

                .ui-slider-handle {
                    background-color: $default-text-color;
                    border-radius: 50%;
                    cursor: pointer;
                    height: 13px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -6px;
                    width: 13px;
                    z-index: 2;

                    &:last-child {
                        @include mkdfTransform(translateX(-50%));
                    }
                }

                .ui-slider-range {
                    background-color: $default-text-color;
                    display: block;
                    height: 3px;
                    position: absolute;
                    top: -1px;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 32px 0 0;

            .button {
                //float: left;

                @include mkdfButtonOutlineColor();

                &:hover {
                    @include mkdfButtonOutlineHoverColor();
                }
            }

            .price_label {
                @include mkdfH5();

                //float: left;
                line-height: 44px;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: $thumb;
                position: relative;

                a {
                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        top: $list-item-padding;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 0 0 0 #{$thumb + $inner-padding};
                        position: relative;
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart,
    &.widget_recent_reviews {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include mkdfH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;

            &:after {
                content: "\f00d";
                display: block;
                font-family: 'FontAwesome';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include mkdfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include mkdfButtonSimple();

                &:hover {
                    @include mkdfButtonSimple();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}

//Filter by price legacy
.widget.widget_block {

    .wp-block-woocommerce-price-filter,
    .wp-block-woocommerce-filter-wrapper {
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            margin-bottom: 33px;
            margin-top: 0;
            padding: 0;
        }
    }

    .wp-block-woocommerce-filter-wrapper {

        .wp-block-woocommerce-price-filter {

            .wc-block-components-price-slider__range-text .wc-block-components-formatted-money-amount,
            .wc-block-price-filter__range-text.wc-block-components-price-slider__range-text {
                color: #000;
                font-size: 12px;
                font-weight: 500;
                letter-spacing: .25em;
                text-transform: uppercase;
                font-family: $default-text-font;
                margin: 8px 0;
            }

            .wc-block-components-price-slider__actions {
                justify-content: flex-start;

                button {
                    font-size: 12px;
                    padding: 14px 55px;
                    border: 1px solid #000;
                    cursor: pointer;
                    z-index: 3;
                    -webkit-transition: color .2s ease-in-out,background-color .2s ease-in-out,border-color .2s ease-in-out;
                    transition: color .2s ease-in-out,background-color .2s ease-in-out,border-color .2s ease-in-out;
                    text-decoration: none;
                    text-transform: uppercase;
                    letter-spacing: .25em;
                    font-weight: 700;
                    line-height: 1em;

                    &:hover {
                        @include mkdfButtonOutlineHoverColor();
                    }
                }
            }

            .wc-block-components-price-slider__range-input-wrapper {

                &:before {
                    height: 1px;
                    background: #000;
                    opacity: 1;
                }
            }

            .wc-block-price-filter__range-input-progress {
                height: 3px;
                top: -1px;
            }
        }
    }

    // can’t comma-separate these type of selectors
    .wc-block-components-price-slider__range-input {

        &::-webkit-slider-thumb {
            background-color: #000;
            border: 0;
        }

        &::-moz-range-thumb {
            background-color: #000;
            border: 0;
        }

        &::-ms-thumb {
            background-color: #000;
            border: 0;
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */