/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.woocommerce-pagination {
    display: block;
    padding: 0;
    position: relative;
    vertical-align: top;
    margin: 0 0 66px;
    text-align: center;

    .page-numbers {
        display: inline-block;
        margin: 0;
        padding: 0;
        position: relative;
        vertical-align: top;
        width: 100%;

        li {
            display: inline-block;
            margin: 0;
            position: relative;
            vertical-align: top;

            a,
            span {
                @include mkdfH5();

                color: $default-text-color;
                cursor: pointer;
                display: inline-block;
                margin: 0 10px;
                padding: 0;
                position: relative;
                text-decoration: none;
                vertical-align: top;
                white-space: nowrap;
                width: auto;

                @include mkdfTransition(color .2s ease-out);

                &:hover,
                &.current {
                    text-decoration: underline;
                }
            }

            a {
                &.next,
                &.prev {
                    font-size: 0;
                    line-height: 1;
                    margin: 0 12px;
                    top: -2px;

                    &:before {
                        display: block;
                        font-family: 'FontAwesome';
                        font-size: 20px;
                        line-height: inherit;
                        position: relative;
                        top: -1px;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }

                &:hover {
                    &.next,
                    &.prev {
                        text-decoration: none;
                        color: $first-main-color;
                    }
                }

                &.prev {
                    &:before {
                        content: "\f104";
                    }
                }

                &.next {
                    &:before {
                        content: "\f105";
                    }
                }
            }
        }
    }
}

.mkdf-woo-view-all-pagination {
    display: table-cell;
    position: relative;
    vertical-align: top;
    width: 1%;

    a {
        color: $default-text-color;
        display: inline-block;
        line-height: 1em;
        position: relative;
        vertical-align: top;
        white-space: nowrap;

        @include mkdfH5();

        &:before {
            color: $default-text-color;
            content: '|';
            display: inline-block;
            font-size: 10px;
            padding: 0 12px 0 0;
            vertical-align: top;
        }

        &:hover {
            color: $first-main-color;
        }
    }
}

/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */