/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.mkdf-content .woocommerce {
    @include mkdfRelativeHolderLayout();
}

.mkdf-pl-main-holder {
    margin: 0 0 77px;

    .mkdf-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    clear: both;
    list-style: none;
    margin: 0 -15px;

    > .product {
        box-sizing: border-box;
        display: inline-block;
        float: left;
        margin: 0 0 30px;
        padding: 0 15px;
        position: relative;
        vertical-align: middle;

        .touchevents & {
            cursor: pointer;
        }

        &:hover {
            .mkdf-pl-inner .mkdf-pl-text {
                opacity: 1;
            }
        }

        .mkdf-pl-category {
            margin-bottom: 3px;

            a {
                @include mkdfH5();

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .mkdf-product-list-title {
            margin: 0 0 5px;
        }

        .mkdf-pl-rating-holder {
            @include mkdfRelativeHolderLayout();

            line-height: 1;
            margin: 10px 0 3px;
        }

        .mkdf-pl-inner {
            @include mkdfRelativeHolderLayout();

            overflow: hidden;

            .mkdf-pl-image {
                @include mkdfRelativeHolderLayout();

                overflow: hidden;

                img {
                    display: block;
                    width: 100%;
                }
            }

            > a {
                background-color:#000;
                opacity:0;
                @include mkdfTransition(all .3s ease-out);
                @include mkdfAbsoluteHolderLayout();

                &:hover {
                    opacity:0.4;
                }
            }
        }

        .mkdf-pl-text-wrapper {
            @include mkdfRelativeHolderLayout();

            margin: 28px 0 24px;
            text-align: center;
        }

        .mkdf-pl-cart-price-holder {
            @include mkdfProductListCartPrice();
        }

        &:hover {
            .price {
                opacity: 0;
                @include mkdfTransition(opacity .1s ease-out 0s);
            }

            .button,
            .added_to_cart.wc-forward {
                position:relative;
                text-decoration:none;
                width:auto;
                left:auto;
                opacity: 1;
                @include mkdfTransition(opacity .2s ease-out .15s);

                &:before,
                &:after {
                    content:"";
                    position:absolute;
                    bottom:12px;
                    left:0;
                    width:100%;
                    height:1px;
                    background-color:currentColor;
                }

                &:before {
                    transform-origin: right;

                    @include mkdfTransition(all .2s ease-in 0s);
                }

                &:after {
                    transform-origin: left;

                    @include mkdfTransform(scale(0, 1));
                    @include mkdfTransition(all .25s ease-out .25s);
                }

                &:hover {
                    &:before {
                        @include mkdfTransform(scale(0, 1));
                    }

                    &:after {
                        @include mkdfTransform(scale(1, 1));
                    }
                }
            }
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.mkdf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width, $i+1);
        $column_width_margin: $column_width * 2;

        ul.products {
            @if ($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }

            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    $product_list_columns: ('columns-3', 'columns-4');

    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);

        > .products {
            > .product {

                @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                    .mkdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;

                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    &.columns-1 {
        > .products {
            > .product {
                float: none;
                width: 100%;
            }
        }
    }

    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');

    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);

            > .products {
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};

                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */